#tpl {
    h1,
    .h1 {
        font-size: max(vwvh(62), rem(62));
        font-family: $font-title;
        font-weight: $font-weight-title;
        line-height: 1.22;

        @media (--viewport-portrait-layout) {
            font-size: rem(40, true);
        }

        @media (--viewport-mobile) {
            font-size: vw(30, true);
        }
    }
}
