#tpl {
    h3,
    .h3 {
        text-transform: uppercase;
        font-size: rem(16);
        font-family: $font-title;
        font-weight: $font-weight-title;
        text-align: center;
        margin-bottom: rem(8);

        @media (--viewport-portrait-layout) {
            margin-bottom: rem(15, true);
            margin-bottom: rem(8, true);
        }
    }
}
