#tpl {
    .header {
        width: 100%;
        height: vw(462);
        background-image: url("~/assets/images/header.jpg");
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        padding: vw(168) calc(var(--base-padding) * 0.2) vw(184);
        display: flex;
        justify-content: center;
        color: $color-light;

        @media (--viewport-mobile-pt) {
            height: 100vw;
            background-image: url("~/assets/images/header-pt.jpg");
            padding: vw(148, true) vw(23, true) vw(174, true);
        }
    }
}
