#tpl {
    .hero {
        // themes
        &--ardzhan {
            .hero__main {
                background-color: $color-ardzhan;
                color: $color-light;
            }
        }

        &--lana {
            .hero__main {
                background-color: $color-lana;
            }
        }

        &--jos {
            .hero__main {
                background-color: $color-jos;
            }
        }

        &--james {
            .hero__main {
                background-color: $color-james;
                color: $color-light;
            }
        }

        &.video-first {
            .hero-intro__text {
                order: 2;
            }

            .hero-intro__video {
                order: 1;
            }
        }

        @media (--viewport-portrait-layout) {
            &.video-first {
                .hero-intro__text {
                    order: 1;
                }

                .hero-intro__video {
                    order: 2;
                }
            }

            &.pt-video-first {
                .hero__main {
                    padding-top: 0;
                }

                .hero-intro__text {
                    order: 2;
                }

                .hero-intro__video {
                    order: 1;
                }
            }

            &.pt-order-1 {
                order: 1;
            }

            &.pt-order-2 {
                order: 2;
            }

            &.pt-order-3 {
                order: 3;
            }

            &.pt-order-4 {
                order: 4;
            }
        }

        &__main {
            padding: rem(101) vw(258);

            @media (--viewport-desktop-m) {
                padding: rem(101) vw(200);
            }

            @media (--viewport-desktop-s) {
                padding: rem(101) vw(120);
            }

            @media (--viewport-portrait-layout) {
                padding: rem(45, true) 0 rem(77, true);
            }

            @media (--viewport-desktop-l) {
                padding: rem(101) rem(258);
            }
        }

        &__inside-1 {
            margin: 0 auto;
            max-width: calc(#{$maxWidthDp} * 1px - #{vw(258)} * 2);

            @media (--viewport-desktop-l) {
                max-width: calc(#{$maxWidthDp} * 1px - #{rem(258)} * 2);
            }
        }

        &-intro {
            padding: 0 vw(68);
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: rem(83);
            margin-bottom: rem(80);

            @media (--viewport-portrait-layout) {
                flex-direction: column;
                padding: 0 rem(90, true);
                gap: rem(51, true);
                margin-bottom: rem(50, true);
            }

            @media (--viewport-mobile-pt) {
                padding: 0 rem(26.5, true);
            }

            &__text {
                text-align: center;
            }

            &__hero-text {
                &--mb {
                    display: none;
                }

                @media (--viewport-mobile) {
                    &--mb {
                        display: block;
                    }

                    &--lp {
                        display: none;
                    }
                }
            }

            &__video {
                width: min(vw(642), rem(642));
                height: min(vw(361), rem(361));
                flex-shrink: 0;

                @media (--viewport-portrait-layout) {
                    width: 100vw;
                    height: vw(210, true);
                }

                @media (--viewport-mobile-pt) {
                    width: 100vw;
                    height: vw(468.5, true);
                }
            }
        }

        &-images {
            $gap: 32;

            display: flex;
            gap: rem($gap);
            margin-bottom: rem(80);

            &__img {
                $w: 685;

                width: calc(50% - rem($gap) * 0.5);

                @media (--viewport-portrait-layout) {
                    width: 100%;
                }
            }

            @media (--viewport-portrait-layout) {
                flex-direction: column;
                padding: 0 rem(120, true);
                margin-bottom: rem(35, true);
            }

            @media (--viewport-mobile-pt) {
                padding: 0 rem(26.5, true);
            }
        }

        &-presentation {
            $gap: rem(32);

            padding: 0 vw(136);

            @media (--viewport-portrait-layout) {
                padding: 0 rem(20, true);
            }

            @media (--viewport-mobile-pt) {
                padding: 0;
            }

            .h2 {
                margin-bottom: rem(45);
                text-align: center;

                @media (--viewport-mobile) {
                    padding: 0 rem(13.5, true);
                    margin-bottom: rem(26, true);
                }
            }

            &__cards {
                position: relative;

                .swiper-wrapper {
                    display: flex;
                    justify-content: space-between;

                    @media (--viewport-portrait-layout) {
                        gap: 0;
                    }
                }
            }

            &__card {
                width: calc(33.33% - $gap * 0.5);

                &-img {
                    width: 100%;
                    margin-bottom: rem(20);
                }

                @media (--viewport-mobile-pt) {
                    width: 100%;
                    flex-shrink: 0;
                }
            }

            .h3 {
                @media (--viewport-mobile) {
                    padding: 0 rem(31.5, true);
                }
            }

            &__text {
                text-align: center;

                @media (--viewport-mobile) {
                    padding: 0 rem(31.5, true);
                }
            }

            &__pagination {
                display: none;
                bottom: 0;
                transform: translateY(calc(100% + rem(25, true)));

                @media (--viewport-mobile-pt) {
                    display: block;
                }

                .swiper-pagination-bullet {
                    width: rem(7, true);
                    height: rem(7, true);
                    background-color: rgba($color-light, 0.3);
                    opacity: 1;
                    transition: background-color 0.2s ease;

                    &.swiper-pagination-bullet-active {
                        background-color: $color-light;
                    }

                    &:not(:last-of-type) {
                        margin-right: rem(5, true);
                    }
                }
            }
        }

        &-goods {
            $gap: rem(80);

            max-width: calc(1px * $maxWidthDp);
            margin: 0 auto;
            padding: rem(100) vw(360);
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;

            @media (--viewport-portrait-layout) {
                padding: rem(45, true) rem(45, true);
            }

            @media (--viewport-mobile-pt) {
                padding: rem(45, true) 0;
            }

            @media (--viewport-desktop-l) {
                padding: rem(100) rem(360);
            }

            &__cards {
                display: flex;
                justify-content: space-between;
                text-align: center;
                margin-bottom: rem(55);

                @media (--viewport-portrait-layout) {
                    margin-bottom: rem(40, true);
                    gap: 0;
                }

                @media (--viewport-mobile-pt) {
                    margin-bottom: rem(20, true);
                }
            }

            &__card {
                width: calc(25% - $gap * 0.4);
                text-decoration: unset;

                @media (--viewport-portrait-layout) {
                    width: 25%;
                }

                @media (--viewport-mobile-pt) {
                    width: 100%;
                    flex-shrink: 0;
                }

                &-img {
                    width: 100%;
                    background-color: rgba($color-dark, 0.5);
                    margin-bottom: rem(25);

                    @media (--viewport-portrait-layout) {
                        width: calc(100% - rem(50, true));
                        margin-bottom: rem(27, true);
                    }
                }

                .h3 {
                    @media (--viewport-portrait-layout) {
                        margin-bottom: rem(12, true);
                    }
                }
            }

            &__pagination {
                display: none;
                bottom: rem(192, true);

                @media (--viewport-mobile-pt) {
                    display: block;
                }

                .swiper-pagination-bullet {
                    width: rem(7, true);
                    height: rem(7, true);
                    background-color: rgb(119 119 118 / 30%);
                    opacity: 1;
                    transition: background-color 0.2s ease;

                    &.swiper-pagination-bullet-active {
                        background-color: #8f8f8f;
                    }

                    &:not(:last-of-type) {
                        margin-right: rem(5, true);
                    }
                }
            }
        }
    }
}
