#tpl {
    h2,
    .h2 {
        text-transform: uppercase;
        font-family: $font-title;
        font-weight: $font-weight-title;
        font-size: rem(26);
        margin-bottom: 1em;

        @media (--viewport-portrait-layout) {
            font-size: rem(20, true);
            margin-bottom: rem(15, true);
        }
    }
}
