#tpl {
    .footer {
        padding: 0 rem(200) rem(100);
        text-align: center;
        max-width: calc(1px * $maxWidthDp);
        margin: 0 auto;

        @media (--viewport-portrait-layout) {
            padding: 0 rem(70, true) rem(70, true);
        }

        @media (--viewport-mobile-pt) {
            padding: 0 rem(16, true) rem(60, true);
        }

        .h2 {
            margin-bottom: rem(55);

            @media (--viewport-mobile-pt) {
                margin-bottom: rem(30, true);
            }
        }

        &__images {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: rem(55);

            @media (--viewport-mobile-pt) {
                flex-direction: column;
                margin-bottom: rem(30, true);
            }
        }

        &__image {
            width: min(rem(445), vw(445));
            max-width: 32%;

            @media (--viewport-mobile-pt) {
                width: 100%;
                max-width: 100%;

                &:not(:last-of-type) {
                    margin-bottom: rem(15, true);
                }
            }
        }
    }
}
